body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.success {
  color: #468847;
}
.error {
  color: #b94a48;
}
.warning {
  color: #c09853;
}
.info {
  color: #3a87ad;
}
.app-header {
  position: fixed;
  height: 25px;
  width: 100%;
  background-color: #eee;
  border-bottom: solid 1px #999;
}
.app-header > div {
  text-align: center;
  padding-top: 3px;
}
.app-content {
  padding-top: 26px;
}

.flexbox {
  display: flex;
}
.flex1 {
  flex: 1 1;
}
.flex2 {
  flex: 2 1;
}
.flex3 {
  flex: 3 1;
}

.random-item textarea {
  width: 100%;
  height: 100px;
}

